import React, { useEffect, useState } from 'react';
import { Button, Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { photoService } from '../../services/photo';
import {
  HomeWrapperElement,
  CardLeftElement,
  CardRightElement,
  CardsWrapperElement,
} from './elements';

export const Home = () => {
  const [lastMarkedPhoto, setLastMarkedPhoto] = useState(null);
  useEffect(() => {
    setLastMarkedPhoto(photoService.getLastMarkedPhoto());
  }, [setLastMarkedPhoto]);
  return (
    <HomeWrapperElement>
      <h4> :</h4>
      <CardsWrapperElement>
        <CardLeftElement body>
          <p>
            ,   ,          
            !
          </p>
          <p>
                   ,  ,  
               ,       ,  
            :)
          </p>
          <p>    ,    Choose your distance</p>
          <p>
               :      (    ,
               )   ,      -  
             .
          </p>
          <p>   8,   8     .</p>
          <p>
               ,        
            .   ,        
            .
          </p>
          <p>
                 ,    {' '}
            <b>10- </b> (   )     :
          </p>
          <ul>
            <li>
              <b></b> - ,        .  
                  . ,   ,   , 
                 ,   ,      
            </li>
            <li>
              <b> </b> -  ,       ,
                ,      .
            </li>
          </ul>
          <p>
                -  .     
            ,         -       .
          </p>
          <p>    .</p>
          <p>
                    ,     
              10  
          </p>
        </CardLeftElement>
        <CardRightElement>
          <Card.Header> </Card.Header>
          {(lastMarkedPhoto &&
            lastMarkedPhoto.look &&
            lastMarkedPhoto.idea &&
            lastMarkedPhoto.participantId &&
            lastMarkedPhoto.nominationId &&
            lastMarkedPhoto.link && (
              <Card.Body>
                <img src={lastMarkedPhoto.link}></img>
                <Card.Text>
                  <div>
                    <b>:</b>
                    {' ' + lastMarkedPhoto.idea}
                  </div>
                  <div>
                    <b>: </b>
                    {' ' + lastMarkedPhoto.look}
                  </div>
                  <div>
                    <Link
                      to={`/photos/${lastMarkedPhoto.nominationId}/${lastMarkedPhoto.participantId}`}
                    >
                      
                    </Link>
                  </div>
                </Card.Text>
              </Card.Body>
            )) || <span className="text-muted"> </span>}
        </CardRightElement>
      </CardsWrapperElement>
      <Button as={Link} to="/nextPhoto" variant="dark">
         
      </Button>
    </HomeWrapperElement>
  );
};
